.text {
    &__h1 {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 700;
        line-height: 1.5;
        font-size: calc(30px + 24 * (100vw / 1920));
        @media (max-width: $sm) {
            font-size: calc(30px + 24 * ((100vw - 360px) / 1920));
        }
    }
    &__h2 {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 600;
        font-size: 2.875rem;
        line-height: 1.5;
    }
    &__h3 {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 600;
        font-size: 1.75rem;
        line-height: 1.8;
    }
    &__h4 {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 600;
        font-size: 1.375rem;
        line-height: 1.5;
    }
    &__h5 {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 500;
        font-size: 1.125rem;
        line-height: 1.5;
    }
    &__p {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 400;
        font-size: 1.125rem;
        line-height: 1.3;
    }
    &__name {
        display: flex;
        align-items: center;
        gap: 30px;
        padding-left: 44px;
    }
    &__title {
        //styleName: Desctop/H2;
        font-family: TT Norms;
        font-style: normal;
        font-weight: 700;
        line-height: 1.5;
        letter-spacing: 0em;
        text-align: left;
        color: #000000;
        font-size: calc(24px + 22 * (100vw / 1920));
        @media (max-width: $sm) {
            font-size: calc(24px + 22 * ((100vw - 360px) / 1920));
        }
    }
    &__number {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 700;
        line-height: 1.5;
        letter-spacing: 0em;
        text-align: left;
        color: $blue;
        font-size: calc(14px + 6 * (100vw / 1920));
        @media (max-width: $sm) {
            font-size: calc(14px + 6 * ((100vw - 360px) / 1920));
        }
    }
}
