.partners {
    $this: &;
    overflow: hidden;
    width: 100%;
    margin-bottom: 120px;
    @media (max-width: $lg) {
        margin-bottom: 50px;
    }
    .container {
    }

    &__inner {
    }

    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 50px;
        @media (max-width: $sm){
            margin-bottom: 20px;
        }
    }

    &__left {
        display: flex;
        align-items: center;
        gap: 20px;
        padding-left: 30px;
        @media (max-width: $sm) {
            flex-direction: column;
            align-items: flex-start;
            gap: 0;
            padding-left: 0;
        }
    }

    &__right {
        display: flex;
        @media (max-width: $sm) {
            display: none;
        }
    }

    &__arrows {
        width: 70px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #dbdbdb;
        background: #fff;
        transition: background 0.2s ease, border 0.2s ease;
        cursor: pointer;
        &:hover {
            background: #118bdd;
            border: 1px solid #118bdd;
            #{$this}__arrow {
                path {
                    stroke: #fff;
                }
            }
        }
    }

    &__arrow {
        path {
            transition: stroke 0.2s ease;
        }
        &--left {
            transform: rotate(180deg);
        }
    }

    &__list {
        display: flex;
        align-items: center;
        gap: 10px;
        overflow: hidden;
    }

    &__items {
        min-width: 218px;
        height: 120px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #fafafa;
        filter: grayscale(100%);
        transition: filter 0.2s ease;
        &:hover {
            filter: none;
        }
    }

    &__img {
    }
}
