.about-us {
    padding: 72px 0;
    position: relative;
    @media (max-width: $lg) {
        padding: 0;
    }
    &__bg {
        top: 0;
        left: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        background: url(../img/slide1.png);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        transition: background 0.2s ease;
        @media (max-width: $lg){
            position: relative;
            display: block;
            height: calc(100vw / 1.6);
        }
    }
    .container {
        position: relative;
        @media (max-width: $lg) {
            background: #fff;
            padding-top: 20px;
            padding-bottom: 50px;
        }
    }
    &__inner {
        background: #fff;
        max-width: 800px;
        padding: 50px;
        @media (max-width: $lg) {
            padding: 0;
        }
    }

    &__title {
        display: flex;
        align-items: center;
        gap: 20px;
        padding-left: 30px;
        margin-bottom: 10px;
        @media (max-width: $sm) {
            flex-direction: column;
            align-items: flex-start;
            gap: 0;
            margin-bottom: 20px;
            padding-left: 0;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 20px;
    }

    &__text {
        color: #434343;
    }

    &__btn {
        display: flex;
        align-items: center;
        gap: 20px;
    }

    &__btn-text {
        font-family: TT Norms;
        font-size: 1rem;
        font-style: normal;
        font-weight: 500;
        line-height: 1.5;
        letter-spacing: 0em;
        text-align: left;
        color: #000;
    }

    &__btn-img {
        width: 8px;
        height: 10px;
    }
    &__arrows {
        display: flex;
        position: absolute;
        top: calc(100% + 32px);
        right: 0;
        @media (max-width: $lg) {
            top: -30px;
        }
    }

    &__arrow {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #118bdd;
        width: 70px;
        height: 40px;
        transition: background 0.2s ease;
        &:hover {
            background: #32a1df;
        }
        @media (max-width: $lg){
            width: 50px;
            height: 30px;
        }
    }

    &__img {
        &--left {
            transform: rotate(90deg);
        }
        &--right {
            transform: rotate(-90deg);
        }
    }
}
