.slider {
    overflow: hidden;
    &__arrow {
        position: absolute;
        bottom: 80px;
        z-index: 100;
        display: none;
        @media (max-width: $lg) {
            display: block;
        }
        @media (max-width: $sm) {
            bottom: 55px;   
        }
        &--left {
            left: 100px;
            @media (max-width: $sm) {
                left: 25px;
            }
        }
        &--right {
            right: 100px;
            @media (max-width: $sm) {
                right: 25px;
            }
        }
    }

    &__img {
        &--left {
            transform: rotate(90deg);
        }
        &--right {
            transform: rotate(-90deg);
        }
    }
}
