.news {
    $this: &;
    padding: 120px 0;
    @media (max-width: $lg) {
        padding: 50px 0;
        padding-bottom: 70px;
    }
    .container {
        position: relative;
    }
    &__inner {
    }

    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left: 30px;
        margin-bottom: 50px;
        @media (max-width: $sm){
            padding-left: 0;
            margin-bottom: 0;
        }
    }

    &__left {
        display: flex;
        align-items: center;
        gap: 20px;
        @media (max-width: $sm) {
            flex-direction: column;
            align-items: flex-start;
            gap: 0;
            margin-bottom: 20px;
            padding-left: 0;
        }
    }

    &__right {
        display: flex;
        align-items: center;
        gap: 10px;

        @media (max-width: $lg) {
            position: absolute;
            top: calc(100% + 20px);
        }
    }

    &__right-text {
        color: #000;
        font-family: TT Norms;
        font-size: 1rem;
        font-style: normal;
        font-weight: 700;
        line-height: 1.1;
        letter-spacing: 0.1em;
        text-align: left;
    }

    &__right-img {
    }

    &__content {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 20px;
        row-gap: 20px;
        @media (max-width: $md) {
            grid-template-columns: 1fr;
        }
    }

    &__item {
        position: relative;
        overflow: hidden;
        &:hover {
            #{$this}__name {
                color: #2991d6;
            }
            #{$this}__img {
                transform: scale(1.2);
            }
        }
    }

    &__img {
        width: 100%;
        height: auto;
        transition: transform 0.2s ease;
    }

    &__item-content {
        width: calc(100% - 180px);
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 30px;
        background: #fafafa;
        @media (max-width: $xl) {
            width: 100%;
            padding: 20px;
        }
    }

    &__name {
        color: #000;
        margin-bottom: 20px;
        transition: color 0.2s ease;
        font-size: calc(18px + 4 * (100vw / 1920));
        @media (max-width: $sm) {
            font-size: calc(18px + 4 * ((100vw - 360px) / 1920));
            white-space: normal;
            margin-bottom: 0;
        }
    }

    &__description {
        color: #434343;
        font-family: TT Norms;
        font-size: 1rem;
        font-style: normal;
        font-weight: 400;
        line-height: 1.5;
        letter-spacing: 0em;
        text-align: left;
        @media (max-width: $xl) {
            display: none;
        }
    }

    &__date {
        width: 180px;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #118bdd;
        position: absolute;
        bottom: 0;
        right: 0;
        @media (max-width: $xl) {
            display: none;
        }
    }

    &__date-text {
        color: #fff;
        font-family: TT Norms;
        font-size: 1rem;
        font-style: normal;
        font-weight: 700;
        line-height: 1.5;
        letter-spacing: 0em;
        text-align: center;
    }
}
