.header {
    $this: &;
    position: absolute;
    z-index: 10;
    width: 100%;
    top: 30px;
    left: 0;
    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        align-items: center;
    }

    &__logo {
        width: 85px;
        height: 27px;
    }

    &__nav {
        display: flex;
        align-items: center;
        gap: 30px;
        user-select: none;
        @media (max-width: $xl) {
            display: none;
        }
    }

    &__items {
        position: relative;
        &--drop {
        }
        &:hover {
            #{$this}__dropdown {
                display: flex;
                flex-direction: column;
                gap: 10px;
            }
            #{$this}__link {
                color: $blue;
            }
            #{$this}__img {
                path {
                    stroke: $blue;
                }
            }
        }
    }

    &__items-top {
        display: flex;
        gap: 10px;
        align-items: center;
        padding-bottom: 10px;
        padding-top: 10px;
    }

    &__link {
        color: #fff;
        font-family: TT Norms;
        font-style: normal;
        font-weight: bold;
        font-size: 0.75rem;
        line-height: 1.1;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        transition: color 0.2s ease;
    }

    &__img {
        path {
            transition: stroke 0.2s ease;
        }
    }

    &__dropdown {
        display: none;
        position: absolute;
        z-index: 11;
        left: 50%;
        transform: translate(-50%);
        top: 100%;
        background: #0c0e16;
        padding: 30px;
    }

    &__dropdown-items {
    }

    &__dropdown-link {
        color: #fff;
        font-family: TT Norms;
        font-size: 1.125rem;
        font-style: normal;
        font-weight: 500;
        line-height: 1.1;
        transition: color 0.2s ease;
        white-space: nowrap;
        &:hover {
            color: $blue;
        }
    }

    &__contact {
        display: flex;
        flex-direction: column;
        gap: 5px;
        @media (max-width: $xl) {
            flex-direction: row;
            gap: 20px;
        }
    }

    &__phone {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 500;
        font-size: 1rem;
        line-height: 1.1;
        color: #fff;
    }

    &__mail {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 500;
        font-size: 0.875rem;
        line-height: 1.2;
        color: #8c94a6;
        @media (max-width: $xl) {
            display: none;
        }
    }
}
