.ways {
    $this: &;
    padding: 120px 0;
    @media (max-width: $lg) {
        padding: 50px 0;
    }
    .container {
    }

    &__inner {
    }

    &__title {
        display: flex;
        align-items: center;
        gap: 20px;
        padding-left: 30px;
        margin-bottom: 50px;
        @media (max-width: $sm){
            flex-direction: column;
            align-items: flex-start;
            gap: 0;
            margin-bottom: 20px;
            padding-left: 0;
        }
    }

    &__list {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 20px;
        @media (max-width: $lg) {
            grid-template-columns: 1fr;
            row-gap: 10px;
        }
    }

    &__items {
        position: relative;
        overflow: hidden;
        @media (max-width: $lg) {
            height: 120px;
        }
        &:hover {
            #{$this}__img {
                transform:  scale(1.2);
                @media (max-width: $lg) {
                    transform: scale(1.2) translate(0, -50%);
                }
            }
        }
    }

    &__img {
        width: 100%;
        height: auto;
        transition: transform 0.2s ease;
        @media (max-width: $lg) {
            position: absolute;
            top: 50%;
            transform: translate(0, -50%);
        }
    }

    &__btn {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        display: flex;
        height: 80px;
        background: #118bdd;
        transition: background 0.2s ease;
        @media (max-width: $lg) {
            height: 50px;
        }
        &:hover {
            background: #32a1df;
        }
    }

    &__name {
        margin-top: auto;
        margin-bottom: auto;
        padding-left: 30px;
        padding-right: 110px;
        color: #fff;
        @media (max-width: $sm){
            font-size: 15px;
        }
    }

    &__arrows {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #32a1df;
        @media (max-width: $lg) {
            width: 50px;
            height: 50px;
        }
    }

    &__arrow {
    }
}
