.our-objects {
    $this: &;
    padding: 120px 0;
    background: #fafafa;
    @media (max-width: $lg) {
        padding: 50px 0;
    }
    .container {
    }
    &__inner {
    }

    &__title {
        display: flex;
        gap: 20px;
        align-items: center;
        padding-left: 30px;
        margin-bottom: 50px;
        @media (max-width: $sm) {
            flex-direction: column;
            align-items: flex-start;
            gap: 0;
            margin-bottom: 20px;
            padding-left: 0;
        }
    }

    &__content {
        display: grid;
        grid-template-columns: 204px 1fr;
        column-gap: 70px;
        align-items: center;
        @media (max-width: 1330px) {
            grid-template-columns: 1fr;
            row-gap: 20px;
        }
    }

    &__left {
        display: flex;
        flex-direction: column;
        margin-left: 40px;
        gap: 100px;
        
        @media (max-width: 1330px) {
            flex-direction: row;
            gap: 50px;
        }
        @media (max-width: $sm){
            margin-left: 0;
        }
        
    }

    &__info {
        position: relative;
    }

    &__num {
        font-family: TT Norms;
        font-style: normal;
        font-weight: 800;
        line-height: 1.175;
        letter-spacing: 0em;
        text-align: left;
        color: #2991d6;
        font-size: calc(50px + 30 * (100vw / 1920));
        @media (max-width: $sm) {
            font-size: calc(50px + 30 * ((100vw - 360px) / 1920));
        }
    }

    &__name {
        white-space: nowrap;
        font-family: TT Norms;
        font-style: normal;
        font-weight: 400;
        line-height: 1.3;
        letter-spacing: 0em;
        text-align: left;
        color: #888888;
        font-size: calc(16px + 2 * (100vw / 1920));
        @media (max-width: $sm) {
            font-size: calc(16px + 2 * ((100vw - 360px) / 1920));
            white-space: pre-wrap;
        }
    }

    &__img {
        position: absolute;
        left: -20px;
        top: 0;
        z-index: -1;
    }

    &__right {
        width: 1030px;
        height: 693px;
        position: relative;
        @media (max-width: 1330px) {
            display: flex;
            overflow-x: scroll;
            width: 100%;
            height: auto;
        }
    }

    &__items {
        position: absolute;
        @media (max-width: 1330px) {
            display: none;
        }
        svg {
            path {
                transition: fill 0.2s ease, stroke 0.2s ease;
            }
        }
        &--active {
            svg {
                path {
                    fill: $blue;
                }
            }
        }
    }
    &__points {
        width: 100%;
        height: 100%;
        &::-webkit-scrollbar {
            height: 0;
        }
        @media (max-width: 1330px) {
            display: flex;
            gap: 10px;
        }
    }

    &__point {
        position: absolute;
        cursor: pointer;
        @media (max-width: 1330px) {
            position: relative;
        }
        &:hover {
            #{$this}__card {
                display: block;
            }
            #{$this}__union {
                path {
                    fill: #fff;
                }
            }
            #{$this}__logo {
                filter: invert(37%) sepia(76%) saturate(2830%) hue-rotate(183deg) brightness(99%) contrast(87%);
            }
        }
    }

    &__union {
        @media (max-width: 1330px) {
            display: none;
        }
        path {
            transition: fill 0.2s ease;
        }
    }

    &__logo {
        cursor: pointer;
        position: absolute;
        left: 7px;
        top: 7px;
        transition: fill 0.2s ease;
        @media (max-width: 1330px) {
            display: none;
        }
    }
    &__card {
        display: none;
        position: absolute;
        z-index: 1000;
        left: 50%;
        transform: translate(-50%);
        top: 100%;
        width: 420px;
        background: #fff;
        @media (max-width: 1330px) {
            display: block;
            position: relative;
            top: 0;
            width: 250px;
        }
    }

    &__card-top {
        background: #1c1b2e;
        height: 250px;
        position: relative;
        @media (max-width: 1330px){
            height: 200px;
        }
    }

    &__card-img {
        width: 100%;
        height: 100%;
        position: absolute;
    }

    &__card-title {
        padding: 20px 20px 6px 20px;
        font-family: TT Norms;
        font-style: normal;
        font-weight: bold;
        font-size: 1.5rem;
        line-height: 1.5;
        color: #000;
    }

    &__card-locate {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 0 20px 6px 20px;
    }

    &__card-point {
    }

    &__card-place {
        font-family: TT Norms;
        font-size: 0.875rem;
        font-style: normal;
        font-weight: 400;
        line-height: 1.5;
        letter-spacing: 0em;
        text-align: left;
    }

    &__card-bot {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 10px;
        row-gap: 10px;
        padding: 0 20px 0 20px;
        @media (max-width: 1333px){
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__card-info {
    }

    &__card-name {
        font-family: TT Norms;
        font-size: 0.874rem;
        font-style: normal;
        font-weight: 400;
        line-height: 1.2;
        letter-spacing: 0em;
        text-align: left;
        color: #8b8b8b;
    }

    &__card-description {
        font-family: TT Norms;
        font-style: normal;
        font-weight: bold;
        font-size: 1.25rem;
        line-height: 1.2;
        color: #000;
    }

    &__card-btn {
        margin: 10px 20px 20px 20px;
        width: 200px;
        height: 50px;
        background: #118bdd;
        display: block;
        border: none;
        display: flex;
        align-items: center;
        padding-left: 20px;
        position: relative;
        transition: background 0.2s ease;
        &:hover {
            background: #32a1df;
        }
    }

    &__card-text {
        color: #fff;
        font-family: TT Norms;
        font-style: normal;
        font-weight: bold;
        font-size: 0.875rem;
        line-height: 1.5;
    }

    &__card-block {
        position: absolute;
        width: 50px;
        height: 50px;
        right: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #32a1df;
    }
    &__card-arrwow {
    }
}
