.footer {
    $this: &;
    background: #0c0e16;
    width: 100%;
    color: #fff;
    padding: 50px 0;
    @media (max-width: $md) {
        padding: 30px 0;
    }
    .container {
    }
    &__inner {
    }

    &__top {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        padding-bottom: 30px;
        position: relative;
        &::after {
            background: #464958;
            content: "";
            display: block;
            position: absolute;
            width: 100vw;
            height: 1px;
            left: 50%;
            transform: translate(-50%);
            bottom: 0;
            @media (max-width: $md) {
                display: none;
            }
        }
        @media (max-width: $md) {
            grid-template-columns: 1fr;
            padding-bottom: 0;
        }
    }
    &__top-items {
        display: flex;
        flex-direction: column;
        position: relative;
        &::after {
            content: "";
            display: none;
            width: 100%;
            height: 1px;
            background: #2c2f37;
            bottom: 0;
            left: 0;

            @media (max-width: $md) {
                display: block;
            }
        }
        &--small {
            display: flex;
            flex-direction: column;
            #{$this}__top-items {
                @media (max-width: $md) {
                    display: none;
                }
            }
            #{$this}__top-items + #{$this}__top-items {
                margin-top: 20px;
            }
        }
        @media (max-width: $md) {
            &--active {
                #{$this}__top-items {
                    display: block;
                }
                #{$this}__top-block {
                    img {
                        transform: rotate(180deg);
                        filter: invert(57%) sepia(36%) saturate(3490%) hue-rotate(177deg) brightness(87%) contrast(91%);
                    }
                }
                #{$this}__top-title {
                    color: $blue;
                }
                #{$this}__top-list {
                    display: flex;
                    padding-bottom: 15px;
                }
            }
        }
    }

    &__top-img {
        height: 35px;
        width: 111px;
    }

    &__top-name {
        font-family: TT Norms;
        font-size: 0.75rem;
        font-style: normal;
        font-weight: 500;
        line-height: 1.5;
        color: #fff;
        margin-top: 10px;
        margin-bottom: 15px;
    }

    &__top-title {
        margin-bottom: 5px;
        font-family: TT Norms;
        font-size: 0.875rem;
        font-style: normal;
        font-weight: 500;
        line-height: 1.4;
        letter-spacing: 0em;
        text-align: left;
        color: #606269;
        transition: color 0.2s ease;
        @media (max-width: $md) {
            font-family: TT Norms;
            font-size: 1rem;
            font-style: normal;
            font-weight: 500;
            line-height: 1.5;
            letter-spacing: 0em;
            text-align: left;
            color: #fff;
        }
    }
    &__top-block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        &--mobile{
            display: none;
            @media (max-width: $md){
                display: flex;
            }
        }
        img {
            display: none;
            height: 20px;
            width: 10px;
            transition: transform 0.2s ease,filter 0.2s ease;
            @media (max-width: $md) {
                display: block;
            }
        }
        @media (max-width: $md) {
            padding: 10px 0;
        }
    }
    &__top-list {
        display: flex;
        flex-direction: column;
        gap: 10px;
        @media (max-width: $md) {
            display: none;
        }
    }

    &__top-link {
        color: #fff;
        font-family: TT Norms;
        font-size: 1rem;
        font-style: normal;
        font-weight: 400;
        line-height: 1.3;
        letter-spacing: 0em;
        text-align: left;
        transition: color 0.2s ease;
        &:hover {
            color: $blue;
        }
        &--big {
            font-family: TT Norms;
            font-size: 1.5rem;
            font-style: normal;
            font-weight: 500;
            line-height: 1.5;
            letter-spacing: 0em;
            text-align: left;
        }
        &--no-hover {
            &:hover {
                color: #fff;
            }
        }
    }

    &__bot {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        margin-top: 30px;
        @media (max-width: $md) {
            grid-template-columns: 1fr;
            row-gap: 10px;
            margin-top: 20px;
        }
    }

    &__bot-items {
    }

    &__bot-link {
        color: #fff;
        font-family: TT Norms;
        font-size: 1rem;
        font-style: normal;
        font-weight: 400;
        line-height: 1.5;
        letter-spacing: 0em;
        text-align: left;
        color: #606269;
        &:hover {
            text-decoration: underline;
        }
    }
}
