.intro {
    width: 100vw;
    height: 100vh;
    position: relative;
    color: white;
    position: relative;
    .container {
        height: 100%;
        position: relative;
    }
    &__video-block {
        &::after {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            z-index: -1;
            background: rgba(#000, 0.8);
        }
    }
    &__video {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        object-fit: cover;
    }
    &__inner {
        height: 100%;
        display: flex;
        align-items: center;
        @media (max-width: $lg) {
            justify-content: center;
            text-align: center;
        }
    }
    &__title {
        color: #fff;
    }

    &__undertitle {
        font-family: TT Norms;
        font-style: normal;
        font-weight: normal;
        line-height: 1.5;
        color: #cfcfcf;
        font-size: calc(18px + 8 * (100vw / 1920));
        @media (max-width: $sm) {
            font-size: calc(18px + 8 * ((100vw - 360px) / 1920));
        }
    }

    &__list {
        position: absolute;
        bottom: 50px;
        right: 0;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 20px;
        row-gap: 20px;
        padding: 0 15px;
        width: 100%;
        @media (max-width: $lg) {
            display: flex;
            gap: 10px;
            transition: right 0.3s linear;
        }
        @media (max-width: $sm){
            bottom: 20px;
        }
    }

    &__items {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        max-width: 330px;
        @media (max-width: $lg) {
            width: 100%;
            min-width: 100%;
            max-width: 100%;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
        }
        @media (max-width: $md) {
            padding: 0 30px;
        }
    }

    &__img {
        width: 30px;
        height: 30px;
    }
    &__text-title {
        font-family: TT Norms;
        font-style: normal;
        font-weight: bold;
        font-size: 1rem;
        line-height: 150%;
        color: #fff;
    }

    &__text-description {
        color: #c9c9c9;
    }
}
